function findShort(s) {
  let m = s.split(' ');
  let str = m[0];
  for (let i = 1; i < m.length; i += 1) {
    if (m[i].length < str.length) {
      str = m[i];
    }
  }
  return str.length;
}
const shortes = findShort('hellow my life javaScpt');
